.doc-container {
    position: relative;
    z-index: 4;
    box-sizing: border-box;

    .floating-root {
        position: fixed;
        top: 0px;
        box-sizing: border-box;

        .fullscreen {
            width:800px;
            height: 596.8px
        }

        .shrink{
            width: 368px;
            height: 640px;
        }

        .doc {
            position: absolute;
            user-select: auto;
            touch-action: none;
            display: inline-block;
            top: 0px;
            right: 0px;
            cursor: auto;
            pointer-events: all;
            z-index: 101;
            transform: translate(294px, 50px);
            max-width: 1024px;
            max-height: 1024px;
            min-width: 368px;
            min-height: 352px;
            box-sizing: border-box;
            background-color: #ffffff;
            padding: 0;
            border: none;
            box-shadow: 0 0 20px 0 rgba(19, 41, 75, .2);

            .doc-center {
                height: 100%;
                font-size: 14px;
                display: flex;
                flex-direction: column;

                .doc-header {
                    cursor: move;
                    flex: none;
                    max-height: 160px;
                    background-color: #006eff;
                    padding: 14px;
                    background-image: url(data:image/svg+xml;base64,);
                    background-repeat: no-repeat;
                    background-position: 100% 100%;
                    box-sizing: border-box;

                    .justify-grid {
                        display: flex;
                        width: 100%;
                        align-items: center;
                        box-sizing: border-box;
                        justify-content: space-between;

                        .left {
                            line-height: 32px;
                            color: #fff;
                            font-size: 16px;
                            white-space: nowrap;
                            text-decoration: none;
                            display: inline-flex;
                            justify-content: flex-start;
                            align-items: center;
                        }

                        .right {
                            flex-grow: 0;
                            flex-shrink: 0;
                            font-size: 16px;
                            line-height: 32px;
                            color: #ffffff;

                            .icon {
                                margin-right: 16px;
                            }
                        }
                    }
                }
            }

            .doc-body {
                flex: auto;

                iframe {
                    width: 100%;
                    height: 100%;
                    overflow: auto;
                    border: 0;
                }
            }

            .doc-footer {
                height: 80px;
                display: flex;
                align-items: center;
                justify-content: center;
                padding:0 16px;
            }
        }
    }
}